.nav {
    grid-area: nav;
    background-color: #99ff99;
    color: #ffffff;
    padding: 20px;
    height: 50em;

}

.item {
    padding: 3px;


}

.item a {
    color: #000000;
    align-self: center;
    text-decoration: none;
    font-size: 15px;
    font-weight: 500;


}
.item a:hover {
    box-shadow: #80ffff;
        color: #45afff;
    background-color: #80ffff ;
}

.item a:active {
    color: deepskyblue;
    box-shadow: #80ffff


}
